<template>
  <b-navbar toggleable type="dark" variant="dark" fixed ="bottom">
    <b-collapse class="collapse" id="nav-collapse" is-nav>
      <b-navbar-nav class="container align-items-center">
        <b-nav-item v-if="job === '1' || job === '0'" to="/register">Daftar</b-nav-item>
        <b-nav-item v-if="job === '0'" to="/login">Login</b-nav-item>
        <b-nav-item v-if="job === '1' || job === '0'" to="/listpo">List PO</b-nav-item>
        <b-nav-item v-if="job === '2' || job === '0'" to="/checklist">Checklist</b-nav-item>
        <b-nav-item v-if="job === '3' || job === '0'" to="/weighing">Timbang</b-nav-item>
        <b-nav-item v-if="job === '1' || job === '0'" to="/setting">Setting</b-nav-item> 
      </b-navbar-nav>
    </b-collapse>
    <b-collapse class="collapse" id="user-collapse" is-nav>
      <b-navbar-nav class="container align-items-left">
        <b-nav-item @click="logout_click">Logout</b-nav-item>
      </b-navbar-nav>
    </b-collapse>
    <b-navbar-toggle class = "mx-4" target="user-collapse">
      <template>
        <b-icon icon="person-fill"></b-icon>
      </template>
    </b-navbar-toggle>
    <b-navbar-toggle class="mx-4" target="nav-collapse"></b-navbar-toggle>
  </b-navbar>
</template>

<script>
export default {
  name: "navigation",
  props: {
  },
  data(){
    return{
      job:localStorage.getItem('job')
    }
  },
  methods : {
    logout_click(){
      localStorage.removeItem('jwt');
      localStorage.removeItem('job');
      localStorage.removeItem('base');
      this.$root.$emit('check_login', false);
      this.$router.push('/login');
    }
  }
};

</script>

