import Vue from "vue";
import VueRouter from "vue-router";
// import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [ 
  {
    path: "/",
    name: "Home",
    component: () =>
      import("../views/Login.vue"),
    meta: {
        guest: true
    }
  },
  {
    path: "/weighing",
    name: "Weighing",
    component: () =>
      import("../views/Weighing.vue"),
    meta: {
      requiresAuth: true,
      is_weighing: true
    }
  },
  {
    path: "/listpo",
    name: "Listpo",
    component: () =>
      import("../views/Listpo.vue"),
    meta: {
      requiresAuth: true,
      is_coordmain: true
    }
  },
  {
    path: "/setting",
    name: "Setting",
    component: () =>
      import("../views/Setting.vue"),
    meta: {
      requiresAuth: true,
      is_coordmain: true
    }
  },
  {
    path: "/login",
    name: "Login",
    component: () =>
      import("../views/Login.vue"),
      meta: {
        guest: true,
    }
  },
  {
    path: "/register",
    name: "Register",
    component: () =>
      import("../views/Register.vue"),
      meta: {
        is_register:true,
        requiresAuth: true,
    }
  },
  {
    path: "/checklist",
    name: "Checklist",
    component: () =>
      import("../views/Checklist.vue"),
      meta: {
        requiresAuth: true,
        is_checklist:true
    }
  },
  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue"),
    meta: {
      guest: true
    }
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  if(to.matched.some(record => record.meta.requiresAuth)) {
    if (localStorage.getItem('jwt') == null) {
      next({
        path: '/Login',
      })
    } 
    else {
      let job = localStorage.getItem('job')
      let base = localStorage.getItem('base')
      if(base == null){
        next({path:'/Login'})
      }
      else {
        if(to.matched.some(record => record.meta.is_coordmain)) {
          if(job === '1' || job === '0'){
              next()
          }
          else{
            next({path: base})
          }
        }
        else if(to.matched.some(record => record.meta.is_checklist)) {
          if(job === '2' || job === '0'){
            next()
          }
          else{
            next({path:base})
          }
          
        }
        else if(to.matched.some(record => record.meta.is_weighing)) {
          if(job === '3' || job === '0'){
            next()
          }
          else{
            next({path: base})
          }
        }
        else if(to.matched.some(record => record.meta.is_register)) {
          if(job === '1' || job === '0'){
            next()
          }
          else{
            next({path: base})
          }
        }
        else {
          next({
            path: '/login',
          })
        } 
      }
      
      // next({
      //   path: '/Login',
      //   params: { nextUrl: to.fullPath }
      // })
    } 
  }
  else{
    if(localStorage.getItem('jwt') == null){
      next()
    }
    else{
      let base = localStorage.getItem('base')
      next({path: base})
    }
  }
})

export default router;
